import react from 'react';
import data from './triviaCategoriesData';
import Dogo from './easterEgg/cuteDoge.jpg';
import Popup from './easterEgg/popUp';

export default function IntroductionPage(props) {
  const [form, setForm] = react.useState({
    quastionNum: 5,
    difficulty: 'easy',
    category: '9',
  });

  //EASTER EGG
  const [isOpen, setIsOpen] = react.useState(false);
  const togglePopup = () => {
    setIsOpen(!isOpen);
  };

  document.addEventListener('keydown', logKey);
  let secretCode = '';
  function logKey(e) {
    secretCode += e.key;

    if (e.key === 'Backspace') {
      secretCode = '';
    }

    if (secretCode.toUpperCase() === 'DAN') {
      console.log('PARTY TIME!');
      togglePopup();
    }

    if (secretCode.length === 5) {
      secretCode = '';
    }

    console.log(secretCode);
  }

  //EASTER EGG END

  function handleChange(event) {
    const { name, value } = event.target;
    setForm((oldForm) => {
      return { ...oldForm, [name]: value };
    });
  }

  //initalize categories from data
  function initCategories() {
    return data.map((category) => {
      return (
        <option key={category.id} value={category.id}>
          {category.name}
        </option>
      );
    });
  }

  return (
    <div className="container ">
      <div className="container text-center">
        <h1 className="pt-4">Quizzical</h1>
        <p>Trivia game about anything and everything!</p>
      </div>

      <div className="container ms-md-5  fw-bold">
        <h3>Game Settings:</h3>
        <div>
          <label className="game-setting-select">
            Number Of Questions:
            <select
              className="form-select"
              onChange={handleChange}
              name="quastionNum"
            >
              <option value="5">5</option>
              <option value="10">10</option>
              <option value="20">20</option>
              <option value="50">50</option>
            </select>
          </label>
        </div>

        <div>
          <label className="game-setting-select">
            Difficulty:
            <select
              className="form-select"
              onChange={handleChange}
              name="difficulty"
            >
              <option value="easy">Easy</option>
              <option value="medium">Medium</option>
              <option value="hard">Hard</option>
            </select>
          </label>
        </div>

        <div>
          <label className="game-setting-select">
            Category:
            <select
              className="form-select"
              onChange={handleChange}
              name="category"
            >
              {initCategories()}
            </select>
          </label>
        </div>

        <input
          className="start-btn mt-2 fw-bold "
          type="submit"
          value="Start Game!"
          onClick={() => props.createApiCall(form)}
        />
      </div>
      {/* EASTER EGG */}
      {isOpen && (
        <Popup
          content={
            <div>
              <h1 className="dogo-words">CUTE DOG</h1>
              <img className="dogo-pic" src={Dogo} alt="test"></img>
              <h1 className="dogo-words">CUTE DOG</h1>
            </div>
          }
          handleClose={togglePopup}
        />
      )}
    </div>
  );
}
