import './App.css';
import IntroductionPage from './components/IntroductionPage';
import TriviaGame from './components/TriviaGame';
import react from 'react';
import axios from 'axios';
import data from './components/tempData';
import { nanoid } from 'nanoid';

function App() {
  const [triviaQuastions, setTriviaQuastions] = react.useState();
  const [showSettings, setShowSettings] = react.useState(false);

  function shuffleArray(array) {
    const tempArray = array;
    for (var i = tempArray.length - 1; i > 0; i--) {
      var j = Math.floor(Math.random() * (i + 1));
      var temp = tempArray[i];
      tempArray[i] = tempArray[j];
      tempArray[j] = temp;
    }
    return tempArray;
  }

  function createQuestions(data) {
    return data.map((triviaQuastion) => {
      let { question, correct_answer } = triviaQuastion;
      return {
        question,
        correct_answer,
        rndAnswers: shuffleArray([
          ...triviaQuastion.incorrect_answers,
          triviaQuastion.correct_answer,
        ]),
      };
    });
  }

  function createUrl(userPrefrences) {
    console.log(userPrefrences);
    const url = `https://opentdb.com/api.php?amount=${userPrefrences.quastionNum}&category=${userPrefrences.category}&difficulty=${userPrefrences.difficulty}&type=multiple`;

    fetchTrivia(url);
  }

  //TODO find a way to intuitively show helpful error message to the user
  async function fetchTrivia(url) {
    try {
      const response = await axios.get(url);
      console.log(response);
      //seperate it into another check api function
      if (response.data.results.length) {
        setTriviaQuastions(createQuestions(response.data.results));
        toggleShowSettings();
      } else {
        console.log('error response is empty');
        alert('there is no such category please try something else :)');
      }
    } catch (e) {
      console.log(e);
    }
  }

  function toggleShowSettings() {
    setShowSettings((oldSate) => !oldSate);
  }

  function mockCreateApiCall() {
    toggleShowSettings();
  }

  return (
    <div className="App container-fluid g-0 page rounded  mt-4">
      {!showSettings && (
        <IntroductionPage createApiCall={createUrl} key={nanoid()} />
      )}
      {showSettings && (
        <TriviaGame
          triviaQuastions={triviaQuastions}
          triviaLength={triviaQuastions.length}
          key={nanoid()}
          resetGame={toggleShowSettings}
        />
      )}
    </div>
  );
}

export default App;
