import AccordionQnA from './AccordionQnA';
import Confetti from 'react-confetti';

export default function EndGamePage(props) {
  function confetti() {
    return (
      <Confetti numberOfPieces={200} run={props.score === props.triviaLength} />
    );
  }

  function stractureTime() {
    let date = new Date(0);
    date.setSeconds(props.time / 1000); // specify value for SECONDS here
    let timeString = date.toISOString().substr(11, 8);
    return timeString;
  }

  function percentage() {
    const fraction = props.score / props.triviaLength;
    return fraction * 100;
  }

  return (
    <div className="container-fluid g-0 p-0">
      {confetti()}
      <div className="container d-flex justify-content-evenly">
        <p>Time taken: {stractureTime()}</p>
        <p>
          Score {props.score}/{props.triviaLength}
        </p>
        <p>Answer rate: {percentage()}%</p>
      </div>
      <AccordionQnA userData={props.userData}></AccordionQnA>
      <div className="container d-flex justify-content-evenly">
        <button onClick={() => props.resetGame()}>TRY AGAIN</button>
        <button>SUBMIT</button>
      </div>
    </div>
  );
}
